.introCont {
  display: block;
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.introCont .semicircle {
  position: absolute;
  top: -30%;
  left: 50%;
  transform: translateX(-50%);
  height: 100vh;
  width: 200vh;
  border-radius: 0 0 150vh 150vh;
  background-color: #2a9d8f;
  z-index: -1;
}

.introCont .introDesc {
  margin-top: 30vh;
  margin-left: 15vw;
  display: flex;
  color: #fff;
}

.introCont .introDesc img {
  height: 40vh;
}

.introCont .introDesc .right {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 1em;
}

.introCont .introDesc .right h1 {
  font-size: 3em;
  font-weight: 600;
  margin-bottom: 0.5em;
}

.introCont .introDesc .right p {
  width: 40vw;
}

.introCont .introDesc .right button {
  position: relative;
  margin: 2em 0 0 0;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  white-space: nowrap;
  color: #fff;
  background: none;
  padding: 10px 45px;
  border-radius: 5px;
  border: none;
  font-size: 1.1em;
  font-weight: 500;
  outline: none;
  cursor: pointer;
  transition: 0.3s all ease;
}

.introCont .introDesc .right button i {
  position: absolute;
  animation: down 0.3s infinite alternate;
}

.introCont .introDesc .right button:hover {
  background-color: #fff;
  color: #2a9d8f;
}

.introCont .introDesc .right button::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
  animation: pulse 1.7s infinite;
}

@keyframes pulse {
  from {
    height: 100%;
    width: 100%;
    opacity: 0;
  }
  to {
    height: 150%;
    width: 110%;
    opacity: 0.5;
  }
}

.introCont .flyingObjects .object {
  position: absolute;
  border-radius: 50%;
  z-index: 1;
}

.introCont .flyingObjects .object1 {
  height: 100px;
  width: 100px;
  background-color: rgba(38, 70, 83, 0.3);
  animation: move1 7s linear infinite alternate;
}

.introCont .flyingObjects .object2 {
  height: 70px;
  width: 70px;
  background-color: rgba(63, 155, 162, 0.8);
  animation: move1 5s linear infinite alternate;
}

.introCont .flyingObjects .object3 {
  height: 50px;
  width: 50px;
  background-color: #264653;
  animation: move2 6s linear infinite alternate;
}

@keyframes move1 {
  0% {
    bottom: 5vh;
    left: 5vw;
  }
  20% {
    bottom: 7vh;
    left: 50vw;
  }
  40% {
    bottom: 25vh;
    left: 90vw;
  }
  60% {
    bottom: 80vh;
    left: 50vw;
  }
  80% {
    bottom: 50vh;
    left: 20vw;
  }
  100% {
    bottom: 10vh;
    left: 10vw;
  }
}

@keyframes move2 {
  0% {
    bottom: 20vh;
    left: 15vw;
  }
  20% {
    bottom: 25vh;
    left: 40vw;
  }
  50% {
    bottom: 25vh;
    left: 90vw;
  }
  70% {
    bottom: 80vh;
    left: 70vw;
  }
  90% {
    bottom: 60vh;
    left: 10vw;
  }
  100% {
    bottom: 10vh;
    left: 10vw;
  }
}

@media only screen and (max-width: 992px) {
  .introCont .introDesc {
    margin-top: 20vh;
    margin-left: 0;
    flex-direction: column-reverse;
    align-items: center;
  }
  .introCont .introDesc img {
    margin-top: 3em;
    height: 30vh;
  }
  .introCont .introDesc .right {
    width: 90vw;
    align-items: center;
  }
  .introCont .introDesc .right h1 {
    font-size: 2em;
    font-weight: 600;
    margin-bottom: 0.5em;
  }
  .introCont .introDesc .right p {
    font-size: 0.95em;
    width: 100%;
  }
  .introCont .introDesc .right button {
    margin: 2em 0 0 0;
    padding: 10px 45px;
    font-size: 0.9em;
  }
  .introCont .introDesc .right button::after {
    position: absolute;
    content: "";
