      opacity: 0.5;
    }
  }
  @keyframes move1 {
    0% {
      bottom: 5vh;
      left: 5vw;
    }
    20% {
      bottom: 7vh;
      left: 50vw;
    }
    40% {
      bottom: 25vh;
      left: 90vw;
    }
    60% {
      bottom: 80vh;
      left: 50vw;
    }
    80% {
      bottom: 50vh;
      left: 20vw;
    }
    100% {
      bottom: 10vh;
      left: 10vw;
    }
  }
  @keyframes move2 {
    0% {
      bottom: 20vh;
      left: 15vw;
    }
    20% {
      bottom: 25vh;
      left: 40vw;
    }
    50% {
      bottom: 25vh;
      left: 90vw;
    }
    70% {
      bottom: 80vh;
      left: 70vw;
    }
    90% {
      bottom: 60vh;
      left: 10vw;
    }
    100% {
      bottom: 10vh;
      left: 10vw;
    }
  }
}
